function CircularBuffer(maxSize) {
    this.maxSize = maxSize;
    this.buffer = [];
}

CircularBuffer.prototype.push = function(item) {
    if (this.buffer.includes(item)) return;
    this.buffer.unshift(item); 
    if (this.buffer.length > this.maxSize) {
        this.buffer.pop();
    }
};


