const topTrackList = document.querySelector('.top-tracks-list')
const getTopTracks = async function(accessToken,artistID) {

    try {
        const res = await 
        fetch(`https://api.spotify.com/v1/artists/${artistID}/top-tracks?market=US`, {
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        });
        const data = await res.json()
        // console.log(data);
    
        if(!res.ok) throw new Error("Something went wrong while loading the artist's top tracks.Try again.")

        let images = []
        for (let i = 0; i < (data.tracks.length < 5? data.tracks.length : 5); i++) {
            const trackFetch = await fetch(`https://api.spotify.com/v1/tracks/${data.tracks[i].id}`, {
                headers: {
                    'Authorization': `Bearer ${accessToken}`
                }});
                const trackData = await trackFetch.json()
            images.push(trackData.album.images[0].url)
        }
    
        topTrackList.innerHTML = ""
        for (let i = 0; i < (data.tracks.length > 5 ? 5 : data.tracks.length); i++) {
            let html = `
            <div class="top-track" data-artist="${data.tracks[i].artists[0].name}" data-title="${data.tracks[i].name}" data-id="${data.tracks[i].id}" data-toptrackid="${data.tracks[i].id}" data-duration="${convertMillisecondsToMinutesAndSeconds(data.tracks[i].duration_ms)}">
            <p class="top-track-no">${i+1}.</p>
            <img src="${images[i]}" class="top-track-image"/>
            <p class="top-track-title">${data.tracks[i].name}</p>
            <p class="top-track-duration">${convertMillisecondsToMinutesAndSeconds(data.tracks[i].duration_ms)}</p>
            </div>
            `
            
        topTrackList.insertAdjacentHTML('beforeend',html)
        }
    
        document.querySelectorAll('.top-track').forEach(track => track.addEventListener('click', async function(e) {
            currentlyPlayingAlbumObject = {}
            recentlyPlayed.push(e.target.closest('.top-track').dataset.id)
            document.title = `Benjo | ${e.target.closest('.top-track').dataset.artist} - ${e.target.closest('.top-track').dataset.title}`;
            playerUI.style.display = 'block';
            clearInterval(lyricsInterval)
            await playTrack(e.target.closest('.top-track').dataset.id);
            playID(`${e.target.closest('.top-track').dataset.artist} ${(e.target.closest('.top-track').dataset.title).replace(symbolsRegex,'')}`);
            highlightTrack(e.target.closest('.top-track'));
    
            fetchLyrics(e.target.closest('.top-track').dataset.title,e.target.closest('.top-track').dataset.artist,e.target.closest('.top-track').dataset.duration)
        }))
    
        const tracks = document.querySelectorAll('.top-track');
        [...tracks].forEach(track => {
            // console.log(track);
            if (track.dataset.id == document.querySelector('.player-title').dataset.trackid) {
                highlightTrack(document.querySelector(`[data-id="${track.dataset.id}"]`))
            }
        })
    
    } catch(err) {
        renderErrorPopup(err)
    }
}

