let currentAlbumName;
const getAlbumID = async function(track) {
    const accessToken = await getToken()
    const res = await fetch(`https://api.spotify.com/v1/tracks/${track}`, { 
        headers: {
            'Authorization': `Bearer ${accessToken}`
        }
    });
    const data = await res.json()
    currentAlbumName = data.album.name
}

const getAlbumHeader = async function(album) {
    const albumHeaderContainer = document.querySelector('.album-header')
    albumHeaderContainer.innerHTML = `
    <div class="album-image-wrapper">
           <img class="album-image album-image-placeholder">
       </div>
       <div class="album-header-bio">
           <div class="album-name-placeholder"></div>
           <div class="album-info-placeholder"></div>
       </div>`
    try {
        const accessToken = await getToken()
        const res = await fetch(`https://api.spotify.com/v1/albums/${album}`, { 
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        });
        const data = await res.json()
        if(!res.ok) throw new Error("Something went wrong while loading the album's header.Try again.")

        // console.log(data);
    
        let html = `
            <div class="album-image-wrapper">
                <img src="${data.images[1].url}" alt="" class="album-image">
            </div>
            <div class="album-header-bio">
                <p class="album-name">${(data.name).length > 50 ? (data.name).substring(0,50) + "..." : data.name}</p>
                <div style="display:flex;"class="album-info"><span class="album-artist-link" data-id="${data.artists[0].id}" >${data.artists[0].name} </span><p>| ${data.release_date} | ${data.total_tracks} tracks</p></div>
            </div>
        `
    
        albumHeaderContainer.innerHTML = html
        const artistLink = document.querySelector('.album-artist-link')
        artistLink.addEventListener('click',function() {
            renderArtistPage(artistLink.dataset.id)
        })
    
        const albumImageWrapper = document.querySelector('.album-image-wrapper')
        if (savedAlbums.includes(data.id)) {
            albumImageWrapper.insertAdjacentHTML('afterbegin',`<ion-icon class="album-like-icon" name="heart"></ion-icon>`)
        } else {
            albumImageWrapper.insertAdjacentHTML('afterbegin',`<ion-icon class="album-like-icon" name="heart-outline"></ion-icon>`)
        }
    
        const iconSelector = document.querySelector('.album-like-icon')
        document.querySelector('.album-like-icon').addEventListener('click',function() {
            if (savedAlbums.includes(data.id)) {
                iconSelector.setAttribute('name','heart-outline')
                let index = savedAlbums.indexOf(data.id)
                savedAlbums.splice(index,1)
            } else {
                iconSelector.setAttribute('name','heart')
                savedAlbums.push(data.id)
            }
            // console.log(savedAlbums);
        })
    } catch(err) {
        renderErrorPopup(err)
    }
}

const symbolsRegex = /[%"?]/g;
let currentTrackNo;
let currentlyPlayingAlbumObject = {}
const addTrackEventListeners = () => {
    document.querySelector('.album-tracks-list').addEventListener('click', async function(e) {
        if (e.target == e.target.closest('.track-like-icon')) return
        currentlyPlayingAlbumObject = {}
        currentTrackNo = e.target.closest('.album-track').dataset.trackno;
        document.title = `Benjo | ${e.target.closest('.album-track').dataset.artist} - ${e.target.closest('.album-track').dataset.title}`;
        playerUI.style.display = 'block';
        clearInterval(lyricsInterval)
        await getAlbumID(e.target.closest('.album-track').dataset.id)
        playTrack(e.target.closest('.album-track').dataset.id);
        playID(`${e.target.closest('.album-track').dataset.artist} - ${(e.target.closest('.album-track').dataset.title).replace(symbolsRegex,'')}`);
        highlightTrack(e.target.closest('.album-track'));
        recentlyPlayed.push(e.target.closest('.album-track').dataset.id)
        fetchLyrics(e.target.closest('.album-track').dataset.title,e.target.closest('.album-track').dataset.artist,e.target.closest('.album-track').dataset.duration)

        const tracks = document.querySelectorAll('.album-track');
        [...tracks].forEach((track,index) => {

            const artist = track.dataset.artist;
            const title = track.dataset.title;
            const duration = track.dataset.duration;
            const id = track.dataset.id;

            const trackDetails = {
                "index" : index+1,
                "artist" : artist,
                "title" : title,
                "duration" : duration,
                "id" : id
            }
            currentlyPlayingAlbumObject[`track${index+1}`] = trackDetails
        });
        console.log(currentlyPlayingAlbumObject);
    });
};

let currentTrackIDs = [];
const getAlbumTracks = async function(album) {
    currentTrackIDs = []
    const albumTrackList = document.querySelector('.album-tracks-list')
    albumTrackList.innerHTML = `
    <div class="album-track">
    <div class="album-track-no top-track-no-placeholder"></div>
    <div style="display: flex; flex-direction: column; gap: .5rem;">
        <div class="album-track-title-placeholder"></div>
        <div class="album-artist-placeholder"></div>
    </div>
    <div class="album-track-duration top-track-duration-placeholder"></div>
    </div>
    `
    try {
        const accessToken = await getToken()
        const res = await fetch(`https://api.spotify.com/v1/albums/${album}/tracks?limit=50`, { 
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        });
        const data = await res.json()
        if(!res.ok) throw new Error("Something went wrong while loading the album's tracks.Try again.")

        albumTrackList.innerHTML = ""
    
        let counter = 0;
        (data.items).forEach(function(track) {
            counter++;
            let html = `
            <div class="album-track" data-trackno="${counter}" data-id="${track.id}" data-artist="${track.artists[0].name}" data-title="${track.name}" data-duration="${convertMillisecondsToMinutesAndSeconds(track.duration_ms)}">
            <p class="album-track-no">${counter}.</p>
            <div class="album-track-title-artist">
                <p class="album-track-title">${track.name}</p>
                <div class="album-track-artist-list track${counter}"></div>
            </div>
            <p class="album-track-duration">${convertMillisecondsToMinutesAndSeconds(track.duration_ms)}</p>
            </div> 
            `
            currentTrackIDs.push(track.id)
            albumTrackList.insertAdjacentHTML('beforeend',html);
            for (let i = 0; i < track.artists.length; i++) {
                document.querySelector(`.track${counter}`).insertAdjacentHTML('beforeend',`<p class="album-track-artist">${track.artists[i].name}`)
            }        
        })
    
        document.querySelectorAll('.album-track').forEach(track => {
            if (savedTracks.includes(track.dataset.id)) {
                track.insertAdjacentHTML('afterbegin',`<ion-icon class="track-like-icon" name="heart"></ion-icon>`)
            } else {
                track.insertAdjacentHTML('afterbegin',`<ion-icon class="track-like-icon" name="heart-outline"></ion-icon>`)
            }
        })
    
        document.querySelectorAll('.track-like-icon').forEach(icon =>
        icon.addEventListener('click',function(e) {
            if (savedTracks.includes(e.target.closest('.album-track').dataset.id)) {
                icon.setAttribute('name','heart-outline')
                let index = savedTracks.indexOf(e.target.closest('.album-track').dataset.id)
                savedTracks.splice(index,1)
            } else {
                icon.setAttribute('name','heart')
                savedTracks.push(e.target.closest('.album-track').dataset.id)
            }
        }))
    
        addTrackEventListeners()
        const tracks = document.querySelectorAll('.album-track');
        [...tracks].forEach(track => {
            if (track.dataset.id == document.querySelector('.player-title').dataset.trackid) {
                highlightTrack(document.querySelector(`[data-id="${track.dataset.id}"]`))
            }
        })
    
    } catch(err) {
        renderErrorPopup(err)
    }

}

export const highlightTrack = function(e) {
    const allTracks = document.querySelectorAll('.album-track')
    const allTopTrack = document.querySelectorAll('.top-track')
    const allSearchTrack = document.querySelectorAll('.search-track')
    const allRecentlyPlayedTrack = document.querySelectorAll('.recently-played-track')
    const allLikedTrack = document.querySelectorAll('.liked-track')
    allTracks.forEach(el => el.classList.remove('selected-track'))
    allTopTrack.forEach(el => el.classList.remove('selected-track'))
    allSearchTrack.forEach(el => el.classList.remove('selected-track'))
    allRecentlyPlayedTrack.forEach(el => el.classList.remove('selected-track'))
    allLikedTrack.forEach(el => el.classList.remove('selected-track'))
    e.classList.add('selected-track')
}

const clearHighlights = function() {
    const allTracks = document.querySelectorAll('.album-track')
    const allTopTrack = document.querySelectorAll('.top-track')
    const allSearchTrack = document.querySelectorAll('.search-track')
    const allRecentlyPlayedTrack = document.querySelectorAll('.recently-played-track')
    const allLikedTrack = document.querySelectorAll('.liked-track')
    allTracks.forEach(el => el.classList.remove('selected-track'))
    allTopTrack.forEach(el => el.classList.remove('selected-track'))
    allSearchTrack.forEach(el => el.classList.remove('selected-track'))
    allRecentlyPlayedTrack.forEach(el => el.classList.remove('selected-track'))
    allLikedTrack.forEach(el => el.classList.remove('selected-track'))
}

const playTrack = async function(track) {
    try {
        const accessToken = await getToken()
        const res = await fetch(`https://api.spotify.com/v1/tracks/${track}`, {
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        })
        if (!res.ok) throw new Error("Couldn't play track. Try again.")
        const data = await res.json()
        // console.log(data);
        updatePlayer(data.album.images[0].url,(data.name).length > 35 ? (data.name).substring(0,35) + "..." : data.name,data.artists[0].name,data.id,data.album.id,data.artists[0].id)
    } catch(err) {
        renderErrorPopup(err)
    }
}

const playerTitle = document.querySelector('.player-title');
const playerArtist = document.querySelector('.player-artist');
let titleClickHandler, artistClickHandler;

const onPlayerTitleClick = function(albumID) {
    return function() {
        if (currentTrackIDs.includes(playerTitle.dataset.trackid) && currentPage == "albumpage") return;
        renderAlbumPage(albumID);
    };
}

const onPlayerArtistClick = function(artistID) {
    return function() {
        renderArtistPage(artistID);
    };
}

const updatePlayer = function(image, trackName, trackArtist, trackID, albumID, artistID) {
    if (titleClickHandler) {
        playerTitle.removeEventListener('click', titleClickHandler);
    }
    if (artistClickHandler) {
        playerArtist.removeEventListener('click', artistClickHandler);
    }

    titleClickHandler = onPlayerTitleClick(albumID);
    artistClickHandler = onPlayerArtistClick(artistID);

    playerTitle.addEventListener('click', titleClickHandler);
    playerArtist.addEventListener('click', artistClickHandler);

    document.querySelector('.player-image').setAttribute('src', image);
    playerTitle.innerText = trackName;
    playerArtist.innerText = trackArtist;
    playerTitle.dataset.trackid = trackID;
    playerTitle.dataset.albumid = albumID;
    playerArtist.dataset.artistid = artistID;
}

