function convertMillisecondsToMinutesAndSeconds(milliseconds) {
    var minutes = Math.floor(milliseconds / 60000);
    var seconds = ((milliseconds % 60000) / 1000).toFixed(0);
    return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
}

const onTrackChange = async function() {
    const track = Object.entries(currentlyPlayingAlbumObject)[currentTrackNo-1][1]
    document.title = `Benjo | ${track.artist} - ${track.title}`;
    playerUI.style.display = 'block';
    await getAlbumID(track.id)
    playTrack(track.id);
    playID(`${track.artist} - ${(track.title).replace(symbolsRegex,'')}`);
    fetchLyrics(track.title,track.artist,track.duration)
    recentlyPlayed.push(track.id)
    clearInterval(lyricsInterval)
    clearHighlights()
    if (document.querySelector(`[data-id="${track.id}"]`) && currentPage == "albumpage") highlightTrack(document.querySelector(`[data-id="${track.id}"]`));
    if (document.querySelector(`[data-likedtrackid="${track.id}"]`) && currentPage == "likedtrackspage") highlightTrack(document.querySelector(`[data-likedtrackid="${track.id}"]`));
    if (document.querySelector(`[data-recentlyplayedid="${track.id}"]`) && currentPage == "recentlyplayedpage") highlightTrack(document.querySelector(`[data-recentlyplayedid="${track.id}"]`));
}

let trackSkip = true;
export const previousTrack = async function() {
    const tracks = Object.values(currentlyPlayingAlbumObject)
    if (currentTrackNo < 2 || !trackSkip || tracks.length == 0) return
    trackSkip = false;
    currentTrackNo--
    onTrackChange()

    setTimeout(() => {
        trackSkip = true;
    }, 300);
}

export const nextTrack = async function() {
    if (!trackSkip) return
    const tracks = Object.values(currentlyPlayingAlbumObject)
    // console.log(currentTrackNo);
    if (tracks.length == 0) return
    if (currentTrackNo > tracks.length-1) return
    trackSkip = false
    currentTrackNo++
    onTrackChange()
    
    setTimeout(() => {
        trackSkip = true;
    }, 300);
}

document.querySelector('.PlayBack').addEventListener('click',previousTrack)
document.querySelector('.PlayForward').addEventListener('click',nextTrack)

// LYRICS

export let lyricsObject;
const lyricsContainer = document.querySelector('.lyrics-container')
const fetchLyrics = async function(title,artist,duration) {
    lyricsContainer.innerHTML = ""
    try {
        const res = await fetch(`https://paxsenixofc.my.id/server/getLyricsMusix?t=${title}&a=${artist}&d=${duration}&type=alternative`)
        if (!res.ok) throw new Error('szar')
        const data = await res.text()
        // console.log(data)
        lyricsObject = createTimestampObject(data);
        // console.log(lyricsObject);
        for (const [timestamp,lyric] of Object.entries(lyricsObject)) {
        lyricsContainer.insertAdjacentHTML('beforeend',`<p class="lyrics">${lyric}</p>`)
    }
} catch(err) {
    lyricsContainer.insertAdjacentHTML('afterbegin',`<p class="lyrics lyrics-active">Something went wrong while gathering the lyrics for this song...</p>
    `)
    }
}


function createTimestampObject(text) {
  const regex = /\[(\d+:\d+\.\d+)\](.+)/g;
  const matches = [...text.matchAll(regex)];
  const keyValuePairs = {};

  matches.forEach(match => {
    const timestamp = extractTimestamp(match[1]);
    const lyrics = match[2].trim();
    keyValuePairs[timestamp] = lyrics;
  });

  return keyValuePairs;
}

function extractTimestamp(lyric) {
    const [minutes, seconds] = lyric.split(':').map(parseFloat);
    const totalSeconds = minutes * 60 + seconds;
    return totalSeconds.toFixed(2);
}

const lyricsParent = document.querySelector('.lyrics-page')
export let lyricsInterval;
export const updateLyricsReadout = function(objectInput,time) {
    let timestamps = Object.keys(objectInput).map(parseFloat);
    let currentIndex = 0;
    let lyricsShown = false;
    const lyricsList = document.querySelectorAll('.lyrics')

    lyricsInterval = setInterval(() => {
        const currentTimestamp = parseFloat(timestamps[currentIndex]);
        
        if (currentTimestamp > time && !lyricsShown) {
            lyricsShown = true;
            // if(currentLyrics >= 0) console.log(lyrics[currentLyrics]);
            // currentLyrics++;
            nextLyric(currentIndex,lyricsList)
        }

        if (time > currentTimestamp) {
            lyricsShown = false;
            currentIndex++;
        }
        time += 0.1;
    }, 100);
};

const renderArea = document.querySelector('.render-area')
const nextLyric = function(currentIndex,list) {
    try {
        let currentLyrics = list[currentIndex-1]
        list.forEach(el => el.classList.remove('lyrics-active'))
        currentLyrics.classList.add('lyrics-active')
        let positionFromTop = currentLyrics.getBoundingClientRect().top - lyricsContainer.getBoundingClientRect().top - (window.innerHeight / 3)
       if (window.getComputedStyle(lyricsParent).display === 'block') {
            renderArea.scrollTo({
            top: positionFromTop,
            behavior: 'smooth'
        });
    }

    } catch(err) {}
    
}