export const showLikedTracksPage = async function() {
    if (window.innerWidth <= 900) closeNav()

    if (currentPage == "likedtrackspage") return
    likedTracksPage.innerHTML = `
    <h2 class="section-title">Liked tracks</h2>
    <p class="discover-prompt liked-tracks-prompt">You haven't liked any tracks yet.</p>
    <div style="height: auto;" class="liked-tracks-container album-track-list">
    `

    if (savedTracks.length > 0) { 
        likedTracksPage.innerHTML = `
        <h2 class="section-title">Liked tracks</h2>
        <div style="height: auto;" class="liked-tracks-container album-track-list">
        `
        for (let i = 0; i < savedTracks.length; i++) {
            let html = `
            <div class="album-track">
            <div class="album-track-no top-track-no-placeholder"></div>
            <div style="display: flex; flex-direction: column; gap: .5rem;">
                <div class="album-track-title-placeholder"></div>
                <div class="album-artist-placeholder"></div>
            </div>
            <div class="album-track-duration top-track-duration-placeholder"></div>
            </div>
            `
            document.querySelector('.liked-tracks-container').insertAdjacentHTML('beforeend',html)
        }
    }
    navs.forEach(el => document.querySelector(`.${el}`).classList.remove('active'))
    likedtracksNav.classList.add('active')
    if (artistPage) artistPage.style.display = 'none'
    if (albumPage) albumPage.style.display = 'none'
    if (discoverPage) discoverPage.style.display = 'none'
    if (lyricsPage) lyricsPage.style.display = 'none'
    if (settingsPage) settingsPage.style.display = 'none'
    if (SearchAllPage) SearchAllPage.style.display = 'none'
    if (recentlyPlayedPage) recentlyPlayedPage.style.display = 'none'
    if (likedAlbumsPage) likedAlbumsPage.style.display = 'none'
    likedTracksPage.style.display = 'block'

    await getLikedTracks()

    const tracks = document.querySelectorAll('.liked-track');
    [...tracks].forEach(track => {
        if (track.dataset.id == document.querySelector('.player-title').dataset.trackid) {
            const index = [...tracks].findIndex(track => track.dataset.id === document.querySelector('.player-title').dataset.trackid)+1;
            highlightTrack(document.querySelector(`[data-likedtrackno="${index}"]`))
        }
    })

    currentPage = "likedtrackspage"
}
likedtracksNav.addEventListener('click', showLikedTracksPage)


