// search tabs

const searchTypes = document.querySelector('.search-types')
const tabIndicators = document.querySelectorAll('.searchtype-tab')
const allIndicator = document.querySelector('[data-tab="all"]')
const albumsIndicator = document.querySelector('[data-tab="albums"]')
const tracksIndicator = document.querySelector('[data-tab="tracks"]')
const artistsIndicator = document.querySelector('[data-tab="artists"]')
const searchAll = document.querySelector('.search-all-tab')
const searchAlbums = document.querySelector('.search-albums-tab')
const searchTracks = document.querySelector('.search-tracks-tab')
const searchArtists = document.querySelector('.search-artists-tab')

searchTypes.addEventListener('click',function(e) {
    const clickedTab = e.target.closest('.searchtype-tab')
    if (!clickedTab) return;
    console.log(searchAll);

    switch (clickedTab.dataset.tab) {
        case "albums":
        if (searchAll) searchAll.style.display = 'none'
        if (searchTracks) searchTracks.style.display = 'none'
        if (searchArtists) searchArtists.style.display = 'none'
        searchAlbums.style.display = 'block'
        tabIndicators.forEach(tab => tab.classList.remove('searchtype-selected'))
        albumsIndicator.classList.add('searchtype-selected')
        break;
        case "tracks":
        if (searchAll) searchAll.style.display = 'none'
        if (searchAlbums) searchAlbums.style.display = 'none'
        if (searchArtists) searchArtists.style.display = 'none'
        searchTracks.style.display = 'block'
        tabIndicators.forEach(tab => tab.classList.remove('searchtype-selected'))
        tracksIndicator.classList.add('searchtype-selected')
        break;
        case "artists":
        if (searchAll) searchAll.style.display = 'none'
        if (searchTracks) searchTracks.style.display = 'none'
        if (searchAlbums) searchAlbums.style.display = 'none'
        searchArtists.style.display = 'block'
        tabIndicators.forEach(tab => tab.classList.remove('searchtype-selected'))
        artistsIndicator.classList.add('searchtype-selected')
        break;
        case "all":
        if (searchArtists) searchArtists.style.display = 'none'
        if (searchTracks) searchTracks.style.display = 'none'
        if (searchAlbums) searchAlbums.style.display = 'none'
        searchAll.style.display = 'block'
        tabIndicators.forEach(tab => tab.classList.remove('searchtype-selected'))
        allIndicator.classList.add('searchtype-selected')
        break;
    
        default:
            break;
    }
})

// color picker


const rootColors = document.querySelector(":root")
const themePicker = document.querySelector('.theme-picker-select')

const changeColor = function(main,mainSub,secondary,secondarySub){
    rootColors.style.setProperty('--main',`#${main}`)
    rootColors.style.setProperty('--main-sub', `#${mainSub}`)
    rootColors.style.setProperty('--secondary',`#${secondary}`)    
    rootColors.style.setProperty('--secondary-sub',`#${secondarySub}`)    

}

themePicker.addEventListener('change',function(e) {
    switch (e.target.value) {
        case "Black":
            changeColor('ececec','b9b9b9','474747','101010')   
            break;
        case "Red":
            changeColor('edd4d4','d6c2c2','b06868','7f3939')
            break;
        case "Purple":
            changeColor('e3d4ed','cfc2d6','9e68b0','6c397f')
            break;  
        case "Pink":
            changeColor('edd4e3','d6c2ce','b0688a','7f3962')
            break;  
        case "Blue":
            changeColor('e0e8ee','c2d4d6','689db0','39697f')
            break;
        case "Brown":
            changeColor('ede0d4','d6ccc2','b08968','7f5539')
            break;
        case "Green":
            changeColor('d4edd7','c2d6c6','68b078','397f52')
        default:
            break;
    }

    localStorage.setItem('color',e.target.value)
})

const colorInit = function(){
    switch (localStorage.getItem('color')) {
        case "Black":
            changeColor('ececec','b9b9b9','474747','101010')   
            break;
        case "Red":
            changeColor('edd4d4','d6c2c2','b06868','7f3939')
            break;
        case "Purple":
            changeColor('e3d4ed','cfc2d6','9e68b0','6c397f')
            break;  
        case "Pink":
            changeColor('edd4e3','d6c2ce','b0688a','7f3962')
            break;  
        case "Blue":
            changeColor('e0e8ee','c2d4d6','689db0','39697f')
            break;
        case "Brown":
            changeColor('ede0d4','d6ccc2','b08968','7f5539')
            break;
        case "Green":
            changeColor('d4edd7','c2d6c6','68b078','397f52')
        default:
            break;
}}

colorInit()

// mobile nav

const navOpener = document.querySelector('.nav-opener')
const navBar = document.querySelector('.nav-bar')
const navOverlay = document.querySelector('.nav-overlay')
const mobileSearchbar = document.querySelector('.mobile-searchbar')
let navOpened = false;

const openNav = function() {
    navBar.style.left = 0
    navOverlay.style.opacity = 1
    navOverlay.style.pointerEvents = 'auto'
    mobileSearchbar.style.opacity = 0
    navOpener.setAttribute('name','close')
    navOpened = true
}

const closeNav = function() {
    navBar.style.left = "-100%"
    navOverlay.style.opacity = 0
    navOverlay.style.pointerEvents = 'none'
    mobileSearchbar.style.opacity = 1
    navOpener.setAttribute('name','menu')
    navOpened = false
}

navOverlay.addEventListener('click',closeNav)

navOpener.addEventListener('click',function() {
    navOpened ? closeNav() : openNav()
})

// error popup renderer

const errorPopupContainer = document.querySelector('.error-popups')

export const renderErrorPopup = function(err) {
    let html = `
    <div class="error-popup">
    <div class="error-alert">
        <p class="error-alert-text">${err}</p>
    </div>
    <div class="error-timer"></div>    
    </div>
    `

    errorPopupContainer.insertAdjacentHTML('beforeend', html);

    const errorPopup = errorPopupContainer.lastElementChild;

    setTimeout(function() {
        if (errorPopup && errorPopup.parentElement) {
            errorPopup.remove();
        }
    }, 5000);
}

setTimeout(() => {
    showDiscoverPage()
}, 150);